import { useState } from 'react';
import { Header } from './components/Layout/Header';
import { ChatInterface } from './components/Chat/ChatInterface';
import { MedicineSearch } from './components/Medicines/MedicineSearch';

function App() {
  const [activeTab, setActiveTab] = useState('chat');

  const renderContent = () => {
    switch (activeTab) {
      case 'chat':
        return <ChatInterface />;
      case 'medicines':
        return <MedicineSearch />;
      case 'pets':
        return (
          <div className="max-w-4xl mx-auto p-4">
            <div className="card text-center">
              <h2 className="text-2xl font-bold text-gray-900 mb-4">
                Mjdzvnieku profili
              </h2>
              <p className="text-gray-600 mb-6">
                 funkcija tiks pievienota drzum. Js varsiet izveidot un prvaldt savu mjdzvnieku profilus.
              </p>
              <div className="text-6xl mb-4"></div>
              <p className="text-sm text-gray-500">
                Pagaidm izmantojiet AI konsultciju un medikamentu datubzi
              </p>
            </div>
          </div>
        );
      default:
        return <ChatInterface />;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <Header activeTab={activeTab} onTabChange={setActiveTab} />
      <main className="pb-8">
        {renderContent()}
      </main>
      
      {/* Footer */}
      <footer className="bg-white border-t border-gray-200 py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <p className="text-gray-600 mb-2">
              AI Veterinrais Asistents - Profesionla paldzba jsu mjdzvniekiem
            </p>
            <p className="text-sm text-gray-500">
               Svargi:  informcija ir tikai informatviem nolkiem un neaizstj profesionlu veterinrrsta konsultciju.
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default App;